  position: relative;
  width: fit-content;
}

.div-wrapper .text-wrapper-7 {
  color: #4078ff;
  font-family: "Inter", Helvetica;
  font-size: 10px;
  font-weight: 600;
  letter-spacing: 0;
  line-height: normal;
  position: relative;
  width: fit-content;
}

.div-wrapper .info-2 {
  align-items: flex-start;
  display: inline-flex;
  flex-direction: column;
  left: 72px;
  position: absolute;
  top: 4px;
}

.div-wrapper .text-wrapper-8 {
  color: #000000;
  font-family: "Noto Sans KR", Helvetica;
  font-size: 18px;
  font-weight: 500;
  letter-spacing: -0.36px;
  line-height: normal;
  margin-top: -1px;
